<script>
export default {
  data() {
    return {
      width: 0,
      currentDate: "",
      test: "",
      fields: [
        {
          key: "qr",
          label: "QR",
          sortable: true,
          thClass: "text-center qr",
          tdClass: "qr"
        },
        {
          key: "login",
          label: "Login",
          sortable: true,
          thClass: "text-center login",
          tdClass: "login"
        },
        {
          key: "logout",
          label: "Logout",
          thClass: "text-center login",
          tdClass: "login"
        },
        {
          key: "minute",
          label: "นาที",
          sortable: true,
          thClass: "text-center minute",
          tdClass: "minute"
        },
        {
          key: "hr",
          label: "ชั่วโมง",
          sortable: true,
          thClass: "text-center hr",
          tdClass: "hr"
        },
        {
          key: "price",
          label: "ค่าบริการ",
          sortable: true,
          thClass: "text-center price",
          tdClass: "price"
        },
        {
          key: "goods8",
          label: "#8",
          thClass: "text-center goods",
          tdClass: "goods"
        },
        {
          key: "goods13",
          label: "#13",
          thClass: "text-center goods",
          tdClass: "goods"
        },
        {
          key: "goods15",
          label: "#15",
          thClass: "text-center goods",
          tdClass: "goods"
        },
        {
          key: "goodsother",
          label: "#",
          thClass: "text-center goodsother",
          tdClass: "goodsother"
        },
        {
          key: "total",
          label: "รวม",
          sortable: true,
          thClass: "text-center price",
          tdClass: "price"
        },
        {
          key: "note",
          label: "หมายเหตุ",
          sortable: true,
          thClass: "text-center note",
          tdClass: "note"
        },
        {
          key: "update",
          label: "update",
          thClass: "update",
          tdClass: "update"
        }
      ],
      fields2: [
        {
          key: "minute",
          label: "นาที"
        },
        {
          key: "price",
          label: "ค่าบริการ"
        },
        {
          key: "goods8",
          label: "#8"
        },
        {
          key: "goods13",
          label: "#13"
        },
        {
          key: "goods15",
          label: "#15"
        },
        {
          key: "goodsother",
          label: "#"
        },
        {
          key: "total",
          label: "รวมค่าใช้จ่าย"
        }
      ],
      items: [],
      items2: [],
      countmax: 7,
      count: 7,
      tablefcous: false
    };
  },
  mounted() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.currentDate = new Date().toJSON().slice(0, 10);
    this.getbydate();
    this.interval = setInterval(() => this.refresh(), 1000);
  },
  watch: {
    items: {
      handler(val) {
        this.count = this.countmax;
        this.items2 = [];
        this.items2.push({
          minute: 0,
          price: 0,
          goods8: 0,
          goods13: 0,
          goods15: 0,
          goodsother: 0,
          total: 0
        });
        for (let i = 0; i < this.items.length; i++) {
          this.items[i].hr = this.timeConvert(this.items[i].minute);
          this.items[i].total =
            parseFloat(this.items[i].price) +
            parseFloat(this.items[i].goods8 * 8) +
            parseFloat(this.items[i].goods13 * 13) +
            parseFloat(this.items[i].goods15 * 15) +
            parseFloat(this.items[i].goodsother);
          //sum
          this.items2[0].minute += parseFloat(this.items[i].minute);
          this.items2[0].price += parseFloat(this.items[i].price);
          this.items2[0].goods8 += parseFloat(this.items[i].goods8);
          this.items2[0].goods13 += parseFloat(this.items[i].goods13);
          this.items2[0].goodsother += parseFloat(this.items[i].goodsother);
          this.items2[0].total += parseFloat(this.items[i].total);
        }
      },
      deep: true
    }
  },
  methods: {
    refresh() {
      if (this.tablefcous) {
        this.count = this.countmax;
        return;
      }
      this.count--;
      if (this.count < 0) {
        this.count = this.countmax;
        this.getbydate();
      }
    },
    handleResize() {
      this.width = window.innerWidth;
    },
    timeConvert(n) {
      var num = n;
      var hours = num / 60;
      var rhours = Math.floor(hours);
      var minutes = (hours - rhours) * 60;
      var rminutes = Math.round(minutes);
      return rhours + " ชม. " + rminutes;
    },
    nextday(day) {
      var d = new Date(this.currentDate);
      var nd = new Date(d);
      nd.setDate(d.getDate() + day);
      this.currentDate = nd.toJSON().slice(0, 10);
      this.getbydate();
    },
    updatebyid(item) {
      const formData = new FormData();
      formData.append("api", "update");
      formData.append("item", JSON.stringify(item));
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          this.$parent.showAlert(response.data.data, "success");
          this.getbydate();
        })
        .catch(e => {});
    },
    updatestatus(id) {
      // console.log(this.items[id]);
      this.items[id].status = !this.items[id].status;
      // this.updatebyid(this.item[id]);
    },
    deletebyid(id) {
      const formData = new FormData();
      formData.append("api", "delete");
      formData.append("id", id);
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          this.$parent.showAlert(response.data.data, "success");
          this.getbydate();
        })
        .catch(e => {});
    },
    getbydate() {
      const formData = new FormData();
      formData.append("api", "list");
      formData.append("date", this.currentDate);
      axios
        .post(this.$store.state.api, formData)
        .then(response => {
          var rows = response.data.rows;
          this.items = rows;
          this.items = [];
          for (let i = 0; i < rows.length; i++) {
            this.items.push({
              id: rows[i].id,
              qr: rows[i].qr,
              login: rows[i].login,
              logout: rows[i].logout,
              minute: rows[i].minute,
              hr: this.timeConvert(rows[i].minute),
              price: rows[i].price,
              goods8: rows[i].goods8,
              goods13: rows[i].goods13,
              goods15: rows[i].goods15,
              goodsother: rows[i].goodsother,
              total: rows[i].total,
              status: rows[i].status,
              note: rows[i].note,
              update: rows[i].id
            });
          }
        })
        .catch(e => {});
    },
    next(web) {
      window.location.href = web;
    }
  }
};
</script>

